.recipe-viewer{
    .info{
        background-color: var(--accent);
        padding: 10px;
        box-sizing: border-box;
        gap: 20px;
        display: flex;
        flex-direction: column;
    }
    h1{
        font-family: 'Quartzo';
        font-size:80px;
        max-width:80%;  
    }
    h2{
        font-family: 'General Sans';
        font-size: 25px;
        font-weight: 600;
        
    }
    h3{
        font-family: 'General Sans';
        font-size:20px;
        font-weight:500;
    }
    .blurb{
        max-width: 80%;
    }
    .ingredients{
        ul{
            list-style: none;
            padding-inline-start:10px;
        }
        
    }
}

@media (max-width:700px) {
    .recipe-viewer{
        .info{
            h1{
                font-size: 50px;
                max-width: 100%;
            }
            .blurb{
                max-width: 90%;
            }
            h2{
                font-size: 20px;
            }
            h3{
                font-size: 15px;
            }
        }
    }
    
}